.menu{
  border-top: 2px solid #75b8e5;
  border-bottom: 2px solid #75b8e5;
  background-color: #0c4f7c;
  background: url('/img/geometric.png') no-repeat;
  box-sizing: content-box;
  height: 100px;
  li{
    display: inline-block;
  }
}
.nav{
  text-align: center;
  font-size: 24px;
  ul{
    margin: 0;
    padding: 0;
    height: 100%;
    a{
      font-size: 24px;
      line-height: 100px;
      display: inline-block;
      position: relative;
      top: -4px;
      padding: 0 10px;
      &.active,
      &:hover{
        box-shadow: inset 0px -5px #f9d100;
      }
    }
  }
}
.logo{
  height: 100px;
  span{
    display: inline-block;
    line-height: 100px;
  }
  .icon{
    font-size: 44px;
    line-height: 100px;
  }
  .name{
    position: relative;
    top: -10px;
    line-height: 100px;
  }
}

.search{
  height: 100px;

  .table{
    height: 100%;
  }
  .table-cell{
    vertical-align: middle;
  }
  button{
    position: absolute;
    right: 29px;
    top: 35px;
    background: none;
    outline: 0px;
    border: 0;
    font-size: 20px;
  }
}

.query {
  position: relative;
  top: -5px;
  border: 1px solid #5fa2cf;
  background-color: #084b78;
  box-shadow: inset 5px 5px rgba(6, 58, 94, 0.3);
  padding: 15px 20px;
  height: 45px;
  font-size: 18px;
  font-weight: 300;
  width: 100%;
  box-sizing: border-box !important;
  &::placeholder{
    color: #f1f2f5;
  }
  &:focus{
    outline: 0;
  }
}
